import './style'
import { Component } from 'preact'
import MobileDetect from 'mobile-detect'

import AndroidButton2 from './components/AndroidButton2'
import AppleButton2 from './components/AppleButton2'

const scheme = 'trustlines'
const host = 'trustlines.network'
let md = {}

if (typeof window !== 'undefined') {
  md = new MobileDetect(window.navigator.userAgent)
}

export default class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      opened: 0
    }
  }

  shouldComponentUpdate() {
    return !this.state.url
  }

  componentDidMount() {
    if (typeof window !== 'undefined') {
      this.setState({
        path: window.location.pathname,
        url: `${scheme}://${host}${window.location.pathname}`
      })
    }
  }

  openApp() {
    // const url = `${scheme}://${host}/${prefix}${path}`
    this.setState({
      opened: ++this.state.opened
    })
    try {
      if (typeof window !== 'undefined') {
        window.location = this.state.url
      }
    } catch (e) {
      console.log(e)
    }
  }

  render(props, state) {
    if (state.path) {
      if (md.os() === 'iOS') {
        this.openApp(state.path)
      }
    }

    return (<div>
      <div className={'logoWrapper'}>
        <img src={require('./assets/logo.png')} className={'logo'} />
        <h2>Trustlines Network</h2>
      </div>
      {state.url && (<div>
        {(md.os() === 'AndroidOS' || md.os() === 'iOS') && (
          <a href={state.url} className={'open-btn'}>Open the app</a>
        )}
        {(md.os() === 'AndroidOS' || !md.mobile()) && (<div>
          <AndroidButton2 href={'https://play.google.com/store/apps/details?id=network.trustlines.mobileapp'} />
        </div>)}
        {(md.os() === 'iOS' || !md.mobile()) && (<div>
          <AppleButton2 href={'https://itunes.apple.com/app/trustlines-network/id1274919729?mt=8'} />
        </div>)}
      </div>)}
    </div>)
  }
}



